.stars:before, .stars:after {
  box-shadow: inherit;
  content: "";
  height: inherit;
  position: absolute;
  width: inherit;
}
.stars:before {
  animation: stars-fade1 3s linear infinite;
  transform: translateZ(-300px);
}
.stars:after {
  animation: stars-fade2 3s linear infinite;
  transform: translateZ(-600px);
}

@keyframes stars-fly {
  from {
    transform: translateZ(0px);
  }
  to {
    transform: translateZ(300px);
  }
}
@keyframes stars-fade1 {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}
@keyframes stars-fade2 {
  from {
    opacity: 0;
  }
  to {
    opacity: 0.5;
  }
}
:root {
  --base-color: #3A1477;
  --color-black: #0a0a0a;
  --color-white: #fafafa;
  --grey-100: color-mix(in oklch, var(--color-black) 90%, var(--color-white));
  --grey-200: color-mix(in oklch, var(--color-black) 80%, var(--color-white));
  --grey-300: color-mix(in oklch, var(--color-black) 70%, var(--color-white));
  --grey-400: color-mix(in oklch, var(--color-black) 60%, var(--color-white));
  --grey-500: color-mix(in oklch, var(--color-black) 50%, var(--color-white));
  --grey-600: color-mix(in oklch, var(--color-black) 40%, var(--color-white));
  --grey-700: color-mix(in oklch, var(--color-black) 30%, var(--color-white));
  --grey-800: color-mix(in oklch, var(--color-black) 20%, var(--color-white));
  --grey-900: color-mix(in oklch, var(--color-black) 10%, var(--color-white));
  --base-100: color-mix(in oklch, var(--base-color) 10%, var(--color-black));
  --base-200: color-mix(in oklch, var(--base-color) 25%, var(--color-black));
  --base-300: color-mix(in oklch, var(--base-color) 50%, var(--color-black));
  --base-400: color-mix(in oklch, var(--base-color) 75%, var(--color-black));
  --base-500: var(--base-color);
  --base-600: color-mix(in oklch, var(--base-color) 80%, var(--color-white));
  --base-700: color-mix(in oklch, var(--base-color) 60%, var(--color-white));
  --base-800: color-mix(in oklch, var(--base-color) 40%, var(--color-white));
  --base-900: color-mix(in oklch, var(--base-color) 20%, var(--color-white));
  --bg-page: var(--base-100);
  --bg-section: var(--base-200);
  --bg-card: var(--base-300);
  --glow-core: oklch(from var(--base-color) calc(l * 2.8) calc(c * 2.5) h);
  --glow-mid: oklch(from var(--base-color) calc(l * 2.0) calc(c * 2.0) h);
  --glow-soft: oklch(from var(--base-color) calc(l * 1.5) calc(c * 1.2) h);
  --gradient-start: oklch(from var(--base-color) calc(l * 1.2) calc(c * 1.8) calc(h - 10));
  --gradient-end: oklch(from var(--base-color) calc(l * 3.0) calc(c * 2.0) calc(h + 5));
  --color-primary: oklch(from var(--base-color) calc(l * 3.10) calc(c * 2.23) calc(h + 3.89));
  --color-secondary: oklch(from var(--base-color) calc(l * 3.60) calc(c * 1.84) calc(h + 2.3));
  --color-accent: oklch(from var(--base-color) calc(l * 5.47) calc(c * 0.61) calc(h - 105.4));
  --color-surface: oklch(from var(--base-color) calc(l * 5.27) calc(c * 0.43) calc(h - 112.33));
  --color-error: oklch(0.58 0.24 29);
  --color-alert: oklch(0.82 0.17 85);
  --color-info: oklch(0.62 0.18 255);
  --color-success: oklch(0.62 0.19 150);
  --action-color: var(--color-primary);
  --action-color-hover: oklch(from var(--action-color) calc(l * 1.1) c h);
  --action-color-active: oklch(from var(--action-color) calc(l * 0.9) c h);
  --border-highlight: var(--color-surface);
}

html {
  background: var(--canvas);
  color: var(--ink);
  scroll-behavior: smooth;
}

body {
  color: inherit;
  container-type: inline-size;
  margin: 0;
  min-height: 100svh;
  position: relative;
  display: grid;
  overflow: clip;
}